}
@media (min-width: 480px) {
  .mobile-phone-container {
    background: #111827;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 2rem;
  }
  .mobile-phone-frame {
    width: 100%;
    max-width: 375px;
    height: 812px;
    background: #000000;
    border-radius: 2.5rem;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.8);
    border: 8px solid #1f2937;
    overflow: hidden;
    position: relative;
  }
  .mobile-phone-frame::before {
    content: '';
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 134px;
    height: 5px;
    background: #333;
    border-radius: 2.5px;
    z-index: 1000;
  }
}
.status-bar {
  padding-top: max(env(safe-area-inset-top), 12px);
  height: calc(44px + max(env(safe-area-inset-top), 12px));
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  background: transparent;
  position: relative;
  z-index: 10;
}
.main-content {
  flex: 1;
