}
.btn-primary {
  background: #e5e5e5;
  color: #000000;
  padding: 16px 24px;
  border-radius: 25px;
  font-weight: 600;
  font-size: 16px;
  border: none;
  width: 100%;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  display: block;
}
.btn-primary:active {
  transform: scale(0.98);
  background: #d4d4d4;
}
.btn-secondary {
  background: transparent;
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 20px;
  border: 1px solid #333333;
  font-weight: 500;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-secondary.active {
  background: #e5e5e5;
  color: #000000;
  border-color: #e5e5e5;
}
.btn-orange {
  background: var(--accent-orange);
  color: #ffffff;
  padding: 16px 24px;
  border-radius: 25px;
  font-weight: 600;
  font-size: 16px;
  border: none;
  width: 100%;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  display: block;
}
