  transform: scale(0.98);
  background: var(--accent-orange-hover);
}
.input-field {
  background: transparent;
  border: none;
  border-bottom: 1px solid #333333;
  color: #ffffff;
  font-size: 16px;
  padding: 12px 0;
  width: 100%;
  outline: none;
  transition: border-color 0.2s ease;
}
.input-field:focus {
  border-bottom-color: var(--accent-orange);
}
.input-field::placeholder {
